body {
  font-family: "Montserrat", sans-serif;
  background-image: url(./assets/day3.jpg);
}
.app {
  text-align: center;
  padding: 30px;
  background-size: auto;
  background-position: initial;
}
.title {
  color: purple;
  margin: auto;
  padding: 20px;
  font-weight: bold;
  /* font-size: 36px; */
}
.input-and-search {
  flex-direction: column;
}
.search-box {
  color: rgb(226, 14, 226);
  padding: 5px;
  border-width: 2px;
  border-radius: 10px;
  border-color: violet;
  margin: auto;
  width: 50%;
  height: 30px;
  margin-inline: 20px;
  font-family: "Montserrat", sans-serif;
  font-weight: bold;
  font-size: large;
  margin-bottom: 30px;
}
.search-box:focus {
  color: rgb(236, 43, 75);
  outline: none;
}
.search {
  border-radius: 10px;
  height: 43px;
  background-color: rgb(226, 14, 226);
  color: white;
  cursor: pointer;
  font-family: "Montserrat", sans-serif;
}
.weather-data {
  width: 80%;
  height: inherit;
  border: 2px solid rgb(226, 14, 226);
  margin: auto;
  border-radius: 10px;
  box-shadow: 0 3px 6px 0 red;
  color: purple;
}
